:root {
    --primary: #fff;
}

.btn {
    padding: 8px 40px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: #fff;
    background-color: var(--primary);
    color: #242424;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: rgb(16, 2, 2);
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 10px;
    font-size: 1rem;
    transform: translate(-28px, 0px);
    border: none;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover {
    transition: all 0.3s ease-out;
    background: rgb(0, 8, 248);
    color: #ffffff;
    transition: 250ms;
}
.navbar {
    background: linear-gradient(90deg, rgb(255, 255, 255) 0%, rgb(99, 202, 225) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .9rem;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
}

.navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
    transform: translate(115px, 0px);
}

.navbar-logo {
    color: rgb(16, 2, 2);
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 1rem;
    font-weight: bold;
    display: flex;
    align-items: center;
    position: absolute;
    transform: translate(-270%, 0);
}

.nav-menu:hover {
    font-size: 1rem;
}

.nav-links:hover {
    background-color: rgb(3, 19, 255);
    color: rgb(255, 255, 255);
    font-weight: bold;
    font-size: 1.2rem;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 0;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 1rem;
    text-transform: uppercase;
}

.nav-item {
    height: 80px;
}

.nav-item ul {
    font-size: 0.8em;
    position: absolute;
    display: none;
    width: inherit;
}

.nav-item ul:hover a {
    display: block;
}

.nav-links {
    color: rgb(16, 2, 2);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}

.nav-links:hover {
    border-bottom: 4px solid rgb(16, 2, 2);
    transition: all 0.2s ease-out;
}

.fa-bars {
    color: rgb(16, 2, 2);
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .NavbarItems {
        position: relative;
    }
    .img-grid img {
        padding-right: 10px;
        border-radius: 40px;
        transform: translate(-5px, -70px);
    }
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 10vh;
        position: absolute;
        bottom: 80vh;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
        background-color: aqua;
    }
    .nav-menu.active {
        background: #242222;
        left: 0;
        opacity: 1;
        position: inherit;
        height: -webkit-fill-available;
        transition: all 0.5s ease;
        /* z-index: 1; */
    }
    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
        background-color: aqua;
    }
    .nav-links:hover {
        background-color: #fff;
        color: #242424;
        border-radius: 0;
    }
    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(-320%, 0);
        font-size: 1.2rem;
    }
    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }
    .fa-times {
        color: rgb(16, 2, 2);
        font-size: 2rem;
    }
    .nav-links-mobile {
        /* display: block; */
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: rgb(16, 2, 2);
        padding: 14px 20px;
        border: 1px solid #fff;
        transition: all 0.3s ease-out;
    }
    .nav-links-mobile:hover {
        background: #fff;
        color: #242424;
        transition: 250ms;
    }
    .dropdown {
        position: relative;
        display: inline-block;
    }
}

