}

h1 {
    text-align: center;
}

.cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1420px;
    width: 100%;
    margin: 0 auto;
}

.cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
}

.cards__items CardItem {
    margin-bottom: 30px;
    background-color: rgb(156, 131, 131);
}

.cards__item {
    display: inline-block;
    flex: 1 1;
    margin: 0 1rem;
    border-radius: 10px;
}

.cards__item__link {
    display: flex;
    flex-flow: column;
    padding: 10px;
    width: 100%;
    box-shadow: 0 6px 20px rgb(5, 5, 5);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
}

.cards__item__pic-wrap {
    position: relative;
    width: 85%;
    padding-top: 100%;
    overflow: hidden;
}

.fade-img {
    -webkit-animation-name: fade-img;
            animation-name: fade-img;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.cards__item__pic-wrap::after {
    content: attr(data-category);
    position: -webkit-sticky;
    position: sticky;
    text-transform: uppercase;
    bottom: 0;
    margin-left: 10px;
    padding: 10px 10px;
    max-width: calc((100%) - 60px);
    font-size: 0.8rem;
    font-weight: bold;
    color: rgb(0, 0, 0);
    background-color: #c4c4c4;
    box-sizing: border-box;
}

.cards__item__img {
    margin: auto;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 120%;
    max-width: 100%;
    height: auto;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
    padding: 10px;
}

.cards__item__img:hover {
    transform: scale(1.1);
}

h5 {
    color: #ffffff;
}

.cards__item__info {
    padding: 20px 130px 30px;
    text-align: center;
}

