:root {
    --primary: #fff;
}

.btn {
    padding: 8px 40px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: rgb(16, 2, 2);
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 10px;
    font-size: 1rem;
    transform: translate(-28px, 0px);
    border: none;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover {
    transition: all 0.3s ease-out;
    background: rgb(0, 8, 248);
    color: #ffffff;
    transition: 250ms;
}